import { Component, OnInit } from '@angular/core'; import { Router, ActivatedRoute } from '@angular/router'; import { Subscription } from 'rxjs/Subscription';  import { Descricao_funcional } from '../../../modelo/Descricao_funcional'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual'; import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'descricao_funcional-dados-details',   templateUrl: 'descricao_funcional_dadosDetails.component.html' }) export class Descricao_funcionalDadosDetailsComponent implements OnInit {    descricao_funcional: Descricao_funcional;    private sub: Subscription;    private errorMessage: string;    constructor(private route: ActivatedRoute, private pesquisaService: PesquisaService) { }    ngOnInit() {       this.sub = this.route.parent.params.subscribe(params => {         let o : ObjetoVirtual = new ObjetoVirtual();         o.classeJava = "br.com.primum.modelo.descricao_funcional.Descricao_funcional";                 let id = +params['id'];         this.descricao_funcional = new Descricao_funcional();         this.descricao_funcional.cd_descricao_funcional = id;         o.objetoJson = this.descricao_funcional;          this.pesquisaService.efetuarPesquisa(o)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);       });   }    onEfetuarPesquisa(result : any) {     if (!result) {       this.errorMessage = 'Houve um erro ao pesquisa a descricao_funcional. '; }      this.descricao_funcional = result;   }    ngOnDestroy() {     this.sub.unsubscribe();   } }


